#calcu {
    background-color:  #ed413e;
    width: 270px;
    height: 290px;
    padding: 40px;
    margin: auto;
    margin-top: 150px;
    /* border-bottom-right-radius: 7px; */
    /* border-top-left-radius: 7px; */
    border-radius: 30px;
    border: 2px solid #ed413e;
  }
  #calcu .operators {
    width: 300px;
    height: 45px;
  }
  #calcu button {
    background-color:#fbcfce;
    width: 50px;
    height: 35px;
    margin-bottom: 200px;
    margin-right: 25px;
    border-radius: 50px;
    margin-top: 25px;
    font-size: 17px;
    border:none;
    
  }
  #calcu button:hover {
    background-color: #f27674;
  }
  
  #calcu button:active {
    background-color: #f27674;
    color:white;
    box-shadow: 0 5px #fce4ec;
    transform: translateY(4px);
  }
  #calcu input {
    background-color: #fafafa;
    width: 230px;
    height: 20px;
    font-size: 30px;
    text-align: end;
    padding: 20px;
    border-radius: 7px;

  }
  #calcu .ac {
    width: 123px;
  }
  #calcu .eq {
    width:123px;
  }