

#loginForm{
    background-color:  #fbcfce;
    width: 250px;
    /* height: 180px; */
    padding: 50px;
    margin: auto;
    margin-top: 180px;
    border: 2px solid  #ed413e;
    border-radius: 3px;
}
#loginForm input{
    width: 250px;
    height: 30px;
    margin-bottom: 30px;

}
#loginForm button{
    width: 250px;
    height: 30px;
    background-color: #ef5350;
    border: none;
    color: white;
}
#loginForm .required::after{
    content:'*';
    color: red;
}