 #page .box {
    background-color:  #fbcfce;
    display: inline-block;
    width: 220px;
    height: 380px;
    padding: 15px;
    margin-right: 50px;
    margin-bottom: 40px;
    margin-left: 50px;
    /* margin: auto; */
    /* margin-top: 110px; */
    border: 2px solid #ed413e;
    border-radius: 5px;
    box-shadow: 4px 4px 4px 4px #ed413e;
    animation:anim 3s forwards;
  }
  #page button {
    width: 60px;
    height: 20px;
    background-color: #ef5350;
    margin-left: 80px;
    margin-top: 2px;
    /* border:none; */
    
  }
  #page button:hover {
    background-color: black;
    color: white;
    transition: 3s;
  }