#page.card {
    background-color: #fbcfce;
    display: inline-block;
    width: 220px;
    height: 360px;
    padding: 15px;
    margin-right: 40px;
    margin-bottom: 40px;
    margin-left: 25px;
    margin-top: 20px;
    /* margin: auto; */
    /* margin-top: 110px; */
    border: 2px solid #ed413e;
    border-radius: 5px;
    box-shadow: 4px 4px 4px 4px #ed413e;
    animation: anim 3s forwards;
    
}

#page button {
    width: 50px;
    height: 30px;
    background-color: #ef5350;
    margin-left: 80px;
    margin-top: 2px;
    /* border:none; */

}

#page button:hover {
    background-color: black;
    color: white;
    transition: 3s;
}

#page table {
    margin-left: 320px;
    width: 600px;
    height: 380px;
    background-color: #ec302c;
    color: white;
   position: absolute;
   top:90px;
    
}